.light {
    background-color: #ffffff;
    color: @light-color;

    a {
        color: @light-color;

        &:hover {
         color: #00b945;
        }
    }

    .hero__introduction {
        color: #00b945;
    }

    .about h2 {
        color: #00b945; 
    }

    .projects h2 {
        color: #00b945;
    }

    .themebutton {
        background-image: url("../../img/svg/moon-icon.svg");

        &:hover {
            background-image: url("../../img/svg/moon-icon-hover.svg")  
        }
    }


    .themebutton:hover ~ .popup-light {
        @media (min-width: @tablet) {
            display: block;
            position: absolute;
            width: 105px;
            height: 25px;
            padding: 0;
            background-color: #cfcfcf;
            color: #111111;
            font-size: 12px;
            top: 64px;
            left: 481px;
            border-radius: 5%;
        }
    }

    .arrow-active {
        border: 6px solid tomato;
        border-left: transparent;
        border-top: transparent;
    }

    .arrowup {
        
        @media (min-width: @tablet) {
            border: 6px solid tomato;
            border-left: transparent;
            border-bottom: transparent;
        }
    }
}