*,
*::before,
*::after {
  box-sizing: border-box;
}

*:focus {
  outline: none;
}

html,
body {
  margin: 0;
  padding: 0;
}

html {
  font-family: "Raleway", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100vw;
  max-width: 1600px;
  margin: 0 auto;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr auto;
  grid-template-areas: "header" "main" "footer";
  overflow-x: hidden;
  position: relative;
}

.main {
  grid-area: main;
  height: 100%;
}

.header {
  grid-area: header;
}

footer {
  grid-area: footer;
}


a {
  text-decoration: none;
  color: @main-color;
}

