button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none;
  display: block;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4 {
  margin: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 100%;
  margin: 0 auto;
}

.layout-wrapper {
  padding: 0 17px 0 17px;

  @media (min-width: @tablet) {
    padding: 0 30px 0 30px;
  }

  @media (min-width: @desktop) {
    padding: 0 60px 0 60px;
  }
}

.modal-opened {
  overflow: hidden;
}
