@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 800;
  src: url("../fonts/raleway/raleway-800.woff2");
  font-display: swap;
}
@font-face {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/rubik/rubik-400.woff2");
  font-display: swap;
}
@font-face {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/rubik/rubik-700.woff2");
  font-display: swap;
}
@font-face {
  font-family: "Dancing";
  font-style: normal;
  font-weight: 800;
  src: url("../fonts/dancing/DancingScript-Bold.woff2");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
*:focus {
  outline: none;
}
html,
body {
  margin: 0;
  padding: 0;
}
html {
  font-family: "Raleway", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}
body {
  width: 100vw;
  max-width: 1600px;
  margin: 0 auto;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr auto;
  grid-template-areas: "header" "main" "footer";
  overflow-x: hidden;
  position: relative;
}
.main {
  grid-area: main;
  height: 100%;
}
.header {
  grid-area: header;
}
footer {
  grid-area: footer;
}
a {
  text-decoration: none;
  color: #d6d6d6;
}
button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none;
  display: block;
}
ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4 {
  margin: 0;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  width: 100%;
  margin: 0 auto;
}
.layout-wrapper {
  padding: 0 17px 0 17px;
}
@media (min-width: 768px) {
  .layout-wrapper {
    padding: 0 30px 0 30px;
  }
}
@media (min-width: 1200px) {
  .layout-wrapper {
    padding: 0 60px 0 60px;
  }
}
.modal-opened {
  overflow: hidden;
}
.header {
  display: flex;
  position: relative;
  padding-top: 15px;
  font-family: "Rubik", "Arial", sans-serif;
}
@media (min-width: 768px) {
  .header {
    flex-direction: row;
    align-items: start;
    justify-content: space-between;
    font-weight: 700;
    padding-top: 32px;
  }
}
.name {
  width: 50%;
  text-align: center;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .name {
    text-align: start;
    width: 20%;
    margin-bottom: 0;
  }
}
.name a {
  font-size: 12px;
}
@media (min-width: 768px) {
  .name a {
    font-size: 14px;
  }
}
.page-nav {
  display: flex;
  align-items: start;
}
@media (max-width: 767px) {
  .page-nav {
    width: 45%;
    justify-content: space-around;
  }
}
.burger-button {
  display: block;
  background-color: transparent;
  width: 20px;
  height: 20px;
  border: none;
  background-image: url("../../img/svg/burger-icon.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100%;
}
.burger-button:hover {
  background-image: url("../../img/svg/burger-white.svg");
}
@media (min-width: 768px) {
  .burger-button {
    display: none;
  }
}
.menu {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  top: 0;
  width: 100%;
  left: 0;
  z-index: 2;
}
@media (min-width: 768px) {
  .menu {
    position: static;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: start;
  }
}
.hidden {
  display: none;
}
@media (min-width: 768px) {
  .hidden {
    display: flex;
  }
}
.menu li {
  width: 100%;
  background-color: #d6d6d6;
  text-align: center;
  padding: 13px;
  border-bottom: 2px solid #5b5b5b;
}
@media (min-width: 768px) {
  .menu li {
    border-bottom: none;
    background-color: transparent;
    margin-left: 75px;
    margin-bottom: 20px;
    padding: 0;
  }
}
.menu a {
  width: 100%;
  white-space: nowrap;
  color: #111111;
  text-align: center;
}
@media (min-width: 768px) {
  .menu a {
    color: #d6d6d6;
    padding: 5px;
  }
}
.page-nav a:hover,
.page-nav a:focus,
.name a:hover,
.name a:focus {
  color: #ffffff;
  border-bottom: 2px solid #ffffff;
}
.page-nav a:active,
.name a:active {
  opacity: 0.5;
}
.themebutton {
  display: block;
  background-color: transparent;
  width: 20px;
  height: 20px;
  border: none;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100%;
  cursor: pointer;
  position: relative;
}
.themebutton:active {
  opacity: 0.5;
}
.popup {
  display: none;
}
.popup-light {
  display: none;
}
.close-menu {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 20px;
  height: 20px;
  border: none;
  background-image: url("../../img/svg/close-line-icon.svg");
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-color: transparent;
}
@media (min-width: 768px) {
  .close-menu {
    display: none;
  }
}
.hero {
  display: flex;
  flex-direction: column;
  height: 100vh;
  align-items: center;
  justify-content: center;
  position: relative;
  font-family: "Rubik", "Arial", sans-serif;
  font-weight: 700;
}
@media (min-width: 768px) {
  .hero {
    margin-bottom: 150px;
  }
}
@media (min-width: 1200px) {
  .hero {
    margin-bottom: 350px;
  }
}
.hero div {
  margin: 0 auto;
  text-align: center;
}
.hero__wowanderful {
  font-family: "Dancing", "Arial", sans-serif;
  font-weight: 800;
  font-size: 46px;
  line-height: 40px;
  position: absolute;
  top: 60px;
  width: 100%;
  height: 80px;
  color: tomato;
}
@media (min-width: 768px) {
  .hero__wowanderful {
    font-size: 68px;
    line-height: 54px;
    top: 80px;
  }
}
.hero-first-p {
  display: flex;
  flex-direction: column;
  margin-top: 0;
}
.hero__whatis {
  font-size: 30px;
  line-height: 30px;
  margin-bottom: 45px;
}
@media (min-width: 768px) {
  .hero__whatis {
    font-size: 52px;
    line-height: 30px;
  }
}
.hero-name {
  font-size: 25px;
  line-height: 30px;
  margin-bottom: 12px;
}
@media (min-width: 768px) {
  .hero-name {
    font-size: 56px;
    line-height: 30px;
    margin-bottom: 45px;
  }
}
.hero__whoiam {
  font-size: 21px;
  line-height: 30px;
}
@media (min-width: 768px) {
  .hero__whoiam {
    font-size: 38px;
  }
}
.hero__wherebased {
  font-size: 16px;
  line-height: 30px;
}
.dark .hero__introduction {
  color: white;
  /* Fallback: assume this color ON TOP of image */
  background-image: url(../../img/granit.jpg);
  background-size: 100%;
  background-position: center;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.arrow-active {
  position: absolute;
  bottom: 100px;
  width: 70px;
  height: 70px;
  left: calc(50% - 35px);
  transform: rotate(45deg);
  animation: blinking-arrow 2s infinite;
}
.dark .arrow-active {
  border: 6px solid #ffffff;
  border-left: transparent;
  border-top: transparent;
}
@keyframes blinking-arrow {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.projects {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: auto;
  position: relative;
  margin-bottom: 60px;
}
@media (min-width: 768px) {
  .projects {
    min-height: 100vh;
    margin-bottom: 150px;
  }
}
@media (min-width: 1200px) {
  .projects {
    margin-bottom: 400px;
  }
}
.projects h2 {
  font-size: 20px;
  line-height: 28px;
  margin-bottom: 40px;
}
@media (min-width: 768px) {
  .projects h2 {
    font-size: 28px;
    line-height: 73px;
    margin-bottom: 45px;
  }
}
.project__list {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto;
  grid-gap: 10px;
}
@media (min-width: 768px) {
  .project__list {
    grid-template-columns: 300px 300px 300px;
    grid-gap: 30px;
  }
}
.project__link {
  position: relative;
}
.project__image-container {
  min-width: 280px;
  height: auto;
  background-color: #8c8c8c;
}
@media (min-width: 768px) {
  .project__image-container {
    width: 300px;
    height: 180px;
  }
}
.project__image-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.project__link p {
  text-align: center;
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .project__link p {
    margin-bottom: 10px;
  }
}
@media (min-width: 1200px) {
  .project__image-container::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: #111;
    opacity: 0.4;
  }
}
@media (min-width: 1200px) {
  .project__image-container:hover {
    position: absolute;
    width: 320px;
    height: 190px;
    top: 0;
    left: -15px;
  }
}
.project__image-container:hover::before {
  display: none;
}
.upbutton {
  position: absolute;
  display: block;
  font-size: 28px;
  width: 80px;
  height: 25px;
  padding: 5px;
  padding-left: 35px;
  padding-top: 0;
  padding-bottom: 10px;
  background-color: #d6d6d6;
  color: black;
  top: 25px;
  left: 15px;
  text-transform: uppercase;
  background-image: url("../../img/svg/up.svg");
  background-repeat: no-repeat;
  background-position: 4px 1px;
  background-size: 31%;
}
.upbutton:hover {
  background-color: tomato;
}
.upbutton:active {
  background-color: white;
}
.arrowup {
  position: absolute;
  top: 50px;
  width: 70px;
  height: 70px;
  right: 40px;
  transform: rotate(-45deg);
}
.about {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 148px;
  position: relative;
}
@media (min-width: 768px) {
  .about {
    min-height: 100vh;
    margin-bottom: 0;
  }
}
@media (min-width: 768px) {
  .about {
    margin-bottom: 300px;
  }
}
.about-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .about-container {
    display: grid;
    grid-template-columns: 25% 75%;
    align-items: start;
  }
}
.about h2 {
  font-size: 40px;
  line-height: 45px;
  margin-bottom: 40px;
}
@media (min-width: 768px) {
  .about h2 {
    font-size: 52px;
    line-height: 73px;
    margin-bottom: 45px;
  }
}
.about__image-container {
  width: 140px;
  height: 140px;
  border-radius: 50%;
  background-color: tomato;
  border: 4px tomato solid;
  overflow: hidden;
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .about__image-container {
    margin-bottom: 0;
  }
}
.about__text-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
}
@media (min-width: 768px) {
  .about__text-block {
    align-items: start;
    text-align: start;
  }
}
.about__name {
  font-size: 32px;
  line-height: 34px;
  margin-bottom: 30px;
  color: tomato;
  text-align: center;
}
@media (min-width: 768px) {
  .about__name {
    text-align: start;
  }
}
.about__item {
  margin-bottom: 30px;
  position: relative;
  width: 100%;
}
.skills__name,
.soft__name,
.tech__name {
  font-size: 24px;
  margin-bottom: 0;
  align-self: center;
}
@media (min-width: 768px) {
  .skills__name,
  .soft__name,
  .tech__name {
    font-size: 28px;
  }
}
@media (max-width: 767px) {
  .about__item p {
    margin-top: 0;
    margin-bottom: 0;
  }
}
.about__download {
  align-self: center;
  text-transform: uppercase;
}
.about__download a {
  background-image: url("../../img/svg/save-icon.svg");
  background-repeat: no-repeat;
  background-position-x: 0%;
  width: 250px;
  display: block;
  padding: 5px;
  padding-left: 50px;
}
.about__download a:hover {
  color: #ffffff;
}
.about__download:active {
  opacity: 0.5;
}
.about__button {
  border: none;
  width: 30px;
  height: 30px;
  background-color: transparent;
  border-radius: 50%;
  position: relative;
  top: -27px;
  left: 239px;
  cursor: pointer;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100%;
}
@media (min-width: 768px) {
  .about__button {
    left: 300px;
    top: -25px;
    left: 270px;
  }
}
.button-open {
  background-image: url("../../img/svg/add-icon.svg");
}
.button-open:hover {
  background-image: url("../../img/svg/add-icon-tomato.svg");
}
.button-close {
  background-image: url("../../img/svg/subtract-color-outline-icon.svg");
}
.button-close:hover {
  background-image: url("../../img/svg/subtract-color-tomato.svg");
}
.payment {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  margin-bottom: 400px;
}
.payment::before {
  position: absolute;
  content: "";
  width: 150px;
  height: 150px;
  top: 130px;
  left: calc(50% - 75px);
  background-image: url("../../img/svg/computer-laptop-icon.svg");
  background-repeat: no-repeat;
  background-position: center center;
}
.payment::after {
  position: absolute;
  content: "";
  width: 120px;
  height: 120px;
  bottom: 130px;
  left: calc(50% - 60px);
  background-image: url("../../img/svg/wallet-icon.svg");
  background-repeat: no-repeat;
  background-position: center center;
}
.payment-wrapper {
  display: grid;
  grid-template-columns: 280px 280px;
  grid-row-gap: 45px;
}
.payment__description {
  grid-column: 1 / 3;
  font-size: 28px;
  text-align: center;
}
.firstlink,
.secondlink {
  font-size: 20px;
}
.secondlink {
  text-align: right;
}
.services {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  padding-top: 40px;
  /*padding here is required to save space for svg image */
}
@media (min-width: 768px) {
  .services {
    min-height: 100vh;
  }
}
.services::before {
  position: absolute;
  content: "";
  width: 150px;
  height: 150px;
  top: -114px;
  left: calc(50% - 75px);
  background-image: url("../../img/svg/computer-laptop-icon.svg");
  background-repeat: no-repeat;
  background-position: center center;
}
@media (min-width: 768px) {
  .services::before {
    top: 0;
  }
}
@media (min-width: 1200px) {
  .services::before {
    top: 130px;
  }
}
.services::after {
  position: absolute;
  content: "";
  width: 120px;
  height: 120px;
  bottom: -145px;
  left: calc(50% - 60px);
  background-image: url("../../img/svg/wallet-icon.svg");
  background-repeat: no-repeat;
  background-position: center center;
}
@media (min-width: 768px) {
  .services::after {
    bottom: 25px;
  }
}
@media (min-width: 1200px) {
  .services::after {
    bottom: 130px;
  }
}
.services-description {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-left: 15px;
  padding-right: 15px;
}
.services-description p {
  font-size: 20px;
  text-align: center;
}
@media (min-width: 768px) {
  .services-description p {
    font-size: 24px;
    text-align: left;
  }
}
.arrowdownfooter {
  display: none;
}
@media (min-width: 1200px) {
  .arrowdownfooter {
    bottom: 30px;
    display: block;
  }
}
.main {
  display: flex;
  flex-direction: column;
}
.footer {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
}
.footer ul {
  display: grid;
  grid-template-columns: 80px 80px;
  grid-gap: 40px;
}
.footer ul li:last-child {
  grid-column: 1 / 3;
  display: flex;
  justify-content: center;
}
.footer ul a {
  display: block;
  width: 60px;
  height: 60px;
  background-repeat: no-repeat;
  background-position: center center;
}
.discord {
  background-image: url("../../img/svg/discord-white-icon-grey.svg");
}
.telegram {
  background-image: url("../../img/svg/telegram-white-icon-grey.svg");
}
.whatsapp {
  background-image: url("../../img/svg/whatsapp-white-icon-grey.svg");
}
.footer-email {
  background-image: url("../../img/svg/at-the-rate-icon-grey.svg");
}
.discord:hover {
  background-image: url("../../img/svg/discord-icon-tomato.svg");
}
.telegram:hover {
  background-image: url("../../img/svg/telegram-icon-tomato.svg");
}
.whatsapp:hover {
  background-image: url("../../img/svg/whatsapp-icon-tomato.svg");
}
.footer-email:hover {
  background-image: url("../../img/svg/at-the-rate-icon-tomato.svg");
}
.discord:active {
  background-image: url("../../img/svg/discord-white-icon.svg");
}
.telegram:active {
  background-image: url("../../img/svg/telegram-white-icon.svg");
}
.whatsapp:active {
  background-image: url("../../img/svg/whatsapp-white-icon.svg");
}
.footer-email:active {
  background-image: url("../../img/svg/at-the-rate-icon.svg");
}
.footer-paypal {
  background-image: url("../../img/svg/paypal-grey.svg");
}
.footer-paypal:hover {
  background-image: url("../../img/svg/paypal-tomato.svg");
}
.footer-paypal:active {
  background-image: url("../../img/svg/paypal-color-icon.svg");
}
.footer-logo {
  line-height: 54px;
  position: absolute;
  bottom: 80px;
  width: 200px;
  display: flex;
  justify-content: center;
  left: calc(50% - 100px);
}
.footer-logo a {
  font-family: "Dancing", "Arial", sans-serif;
  font-weight: 800;
  font-size: 28px;
  text-align: center;
  color: #d6d6d6;
  margin: 0;
}
.footer-logo a:hover {
  color: tomato;
}
.footer-logo a:active {
  color: #ffffff;
}
.dark {
  background-color: #111111;
  color: #d6d6d6;
}
.dark .themebutton {
  background-image: url("../../img/svg/day-sunny-icon.svg");
}
.dark .themebutton:hover {
  background-image: url("../../img/svg/day-sunny-white.svg");
}
@media (min-width: 768px) {
  .dark .themebutton:hover + .popup {
    display: block;
    position: absolute;
    width: 105px;
    height: 25px;
    padding: 0;
    background-color: #cfcfcf;
    color: #111111;
    font-size: 12px;
    top: 64px;
    left: 481px;
    border-radius: 5%;
  }
}
.dark .projects h2 {
  color: white;
  background-image: url(../../img/granit.jpg);
  background-size: 100%;
  background-position: center;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
@media (min-width: 768px) {
  .dark .arrowup {
    border: 6px solid #ffffff;
    border-left: transparent;
    border-bottom: transparent;
  }
}
.dark .about h2 {
  color: white;
  background-image: url(../../img/granit.jpg);
  background-size: 100%;
  background-position: center;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-top: 30px;
}
.light {
  background-color: #ffffff;
  color: #63dc90;
}
.light a {
  color: #63dc90;
}
.light a:hover {
  color: #00b945;
}
.light .hero__introduction {
  color: #00b945;
}
.light .about h2 {
  color: #00b945;
}
.light .projects h2 {
