.projects {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: auto;
    position: relative;
    margin-bottom: 60px;

    @media (min-width: @tablet) {
       min-height: 100vh; 
       margin-bottom: 150px;
    }

    @media (min-width: @desktop) {
        margin-bottom: 400px;
    }
}

.projects h2 {
    font-size: 20px; 
    line-height: 28px;
    margin-bottom: 40px; 

    @media (min-width: @tablet) {
        font-size: 28px;
        line-height: 73px;
        margin-bottom: 45px;
    }
}
.project__list {
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: auto;
    grid-gap: 10px;
    

    @media (min-width: @tablet) {
        grid-template-columns: 300px 300px 300px;
        grid-gap: 30px; 
    }
}

.project__link {
    position: relative
}

.project__image-container {
    min-width: 280px;
    height: auto;
    background-color: #8c8c8c;

    @media (min-width: @tablet) {
       width: 300px;
        height: 180px; 
    }
}

.project__image-container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.project__link p {
    text-align: center;
    margin-bottom: 0;

    @media (min-width: @tablet) {
        margin-bottom: 10px;
    }
}

.project__image-container::before {

    @media (min-width: @desktop) {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-color: #111;
        opacity: 0.4;
    }
}

.project__image-container:hover {

    @media (min-width: @desktop) {
        position: absolute;
        width: 320px;
        height: 190px;
        top: 0;
        left: -15px;
    }

    &::before {
        display: none;
    }
}

.upbutton {
    position: absolute;
    display: block;
    font-size: 28px;
    width: 80px;
    height: 25px;
    padding: 5px;
    padding-left: 35px;
    padding-top: 0;
    padding-bottom: 10px;
    background-color: @main-color;
    color: black;
    top: 25px;
    left: 15px;
    text-transform: uppercase;
    background-image: url("../../img/svg/up.svg");
    background-repeat: no-repeat;
    background-position: 4px 1px;
    background-size: 31%;

    &:hover {
        background-color: tomato;
    }

    &:active {
        background-color: white;
    }
}

.arrowup {
    position: absolute;
    top: 50px;
    width: 70px;
    height: 70px;
    right: 40px;

    transform: rotate(-45deg);
}

