.footer {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
}

.footer ul {
    display: grid;
    grid-template-columns: 80px 80px;
    grid-gap: 40px;
}

.footer ul li:last-child {
    grid-column: 1 / 3;
    display: flex;
    justify-content: center;
}

.footer ul a {
    display: block;
    width: 60px;
    height: 60px;
    background-repeat: no-repeat;
    background-position: center center;
}

.discord {
    background-image: url("../../img/svg/discord-white-icon-grey.svg");
}

.telegram {
    background-image: url("../../img/svg/telegram-white-icon-grey.svg");
}

.whatsapp {
    background-image: url("../../img/svg/whatsapp-white-icon-grey.svg");
}

.footer-email {
    background-image: url("../../img/svg/at-the-rate-icon-grey.svg");
}

.discord:hover {
    background-image: url("../../img/svg/discord-icon-tomato.svg");
}

.telegram:hover {
    background-image: url("../../img/svg/telegram-icon-tomato.svg");
}

.whatsapp:hover {
    background-image: url("../../img/svg/whatsapp-icon-tomato.svg");
}

.footer-email:hover {
    background-image: url("../../img/svg/at-the-rate-icon-tomato.svg");
}

.discord:active {
    background-image: url("../../img/svg/discord-white-icon.svg");
}

.telegram:active {
    background-image: url("../../img/svg/telegram-white-icon.svg");
}

.whatsapp:active {
    background-image: url("../../img/svg/whatsapp-white-icon.svg");
}

.footer-email:active {
    background-image: url("../../img/svg/at-the-rate-icon.svg");
}

.footer-paypal {
    background-image: url("../../img/svg/paypal-grey.svg");
}

.footer-paypal:hover {
    background-image: url("../../img/svg/paypal-tomato.svg");
}

.footer-paypal:active {
    background-image: url("../../img/svg/paypal-color-icon.svg");
}



.footer-logo {    
    line-height: 54px;
    position: absolute;
    bottom: 80px;
    width: 200px;
    display: flex;
    justify-content: center;
    left: calc(50% - 100px);
}

.footer-logo a {
    font-family: "Dancing", "Arial", sans-serif;
    font-weight: 800;
    font-size: 28px;    
    text-align: center;
    color: #d6d6d6;
    margin:0;
}

.footer-logo a:hover {
    color: tomato;
}

.footer-logo a:active {
    color: #ffffff;
}



