.payment {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    margin-bottom: 400px;
    

    &::before {
        position: absolute;
        content: "";
        width: 150px;
        height: 150px;
        top: 130px;
        left: calc(50% - 75px);
        background-image: url("../../img/svg/computer-laptop-icon.svg");
        background-repeat: no-repeat;
        background-position: center center;
    }

    &::after {
        position: absolute;
        content: "";
        width: 120px;
        height: 120px;
        bottom: 130px;
        left: calc(50% - 60px);
        background-image: url("../../img/svg/wallet-icon.svg");
        background-repeat: no-repeat;
        background-position: center center;
    }
}


.payment-wrapper {
    display: grid;
    grid-template-columns: 280px 280px;
    grid-row-gap: 45px;
}

.payment__description {
    grid-column: 1 / 3;
    font-size: 28px;
    text-align: center;
}

.firstlink, .secondlink {
    font-size: 20px;
}

.secondlink {
    text-align: right;
}