.about {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 148px;
    position: relative;

    @media (min-width: @tablet) {
        min-height: 100vh;
        margin-bottom: 0;
    }

    @media (min-width: @tablet) {
        margin-bottom: 300px;
    }
}

.about-container {
    display: flex;
    flex-direction: column;
    align-items: center;

    @media (min-width: @tablet) {
        display: grid;
        grid-template-columns: 25% 75%;
        align-items: start;
    }
}

.about h2 {
    font-size: 40px;
    line-height: 45px;
    margin-bottom: 40px; 

    @media (min-width: @tablet) {
        font-size: 52px;
        line-height: 73px;
        margin-bottom: 45px;
    }
}

.about__image-container {
    width: 140px;
    height: 140px;
    border-radius: 50%;
    background-color: tomato;
    border: 4px tomato solid;
    overflow: hidden;

    margin-bottom: 30px;

    @media (min-width: @tablet) {
        margin-bottom: 0;
    }
}

.about__text-block {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;

    @media (min-width: @tablet) {
        align-items: start;
        text-align: start;
    }
}

.about__name {
    font-size: 32px;
    line-height: 34px;
    margin-bottom: 30px;
    color: tomato;
    text-align: center;

    @media (min-width: @tablet) {
        text-align: start;
        
    }
}

.about__item {
    margin-bottom: 30px;
    position: relative;
    width: 100%;
}

.skills__name, .soft__name, .tech__name {
    font-size: 24px;
    margin-bottom: 0;
    align-self: center;

    @media (min-width: @tablet) {
        font-size: 28px;
    }
}
.about__item p {
    @media (max-width: @mobile-only) {
        margin-top: 0;
        margin-bottom: 0;
    }
}

.about__download {
    align-self: center;
    text-transform: uppercase;
}

.about__download a {
    background-image: url("../../img/svg/save-icon.svg");
    background-repeat: no-repeat;
    background-position-x: 0%;
    width: 250px;
    display: block;
    padding: 5px;
    padding-left: 50px;
}

.about__download a:hover {
    color: #ffffff;
}

.about__download:active {
    opacity: 0.5;
}

.about__button {
    border: none;
    width: 30px;
    height: 30px;
    background-color: transparent;
    border-radius: 50%;
    position: relative;
    top: -27px;
    left: 239px;
    
    cursor: pointer;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 100%;

    @media (min-width: @tablet) {
       left: 300px; 
       top: -25px;
        left: 270px;
    }
}

.button-open {
    background-image: url("../../img/svg/add-icon.svg");
}

.button-open:hover {
    background-image: url("../../img/svg/add-icon-tomato.svg");
}

.button-close {
    background-image: url("../../img/svg/subtract-color-outline-icon.svg");
}

.button-close:hover {
    background-image: url("../../img/svg/subtract-color-tomato.svg");
}