.hero {
    display: flex;
    flex-direction: column;
    height: 100vh;
    align-items: center;
    justify-content: center;
    position: relative;
    font-family: "Rubik", "Arial", sans-serif;
    font-weight: 700;

    @media (min-width: @tablet) {
        margin-bottom: 150px;
    }

    @media (min-width: @desktop) {
        margin-bottom: 350px;
    }
}

.hero div {
    margin: 0 auto;
    text-align: center;
}


.hero__wowanderful {
    font-family: "Dancing", "Arial", sans-serif;
    font-weight: 800;
    font-size: 46px;
    line-height: 40px;
    position: absolute;
    top: 60px;
    width: 100%;
    height: 80px;
    color: tomato; 

    @media (min-width: @tablet) {
        font-size: 68px;
        line-height: 54px;
        top: 80px;
    }
}

.hero-first-p {
    display: flex;
    flex-direction: column;
    margin-top: 0;
}

.hero__whatis {
    font-size: 30px;
    line-height: 30px;
    margin-bottom: 45px;

    @media (min-width: @tablet) {
        font-size: 52px;
        line-height: 30px;  
    }
}

.hero-name {
    font-size: 25px;
    line-height: 30px;
    margin-bottom: 12px;
    

    @media (min-width: @tablet) {
        font-size: 56px;
        line-height: 30px; 
        margin-bottom: 45px;  
    }
}

.hero__whoiam {
    font-size: 21px;
    line-height: 30px;

    @media (min-width: @tablet) {
        font-size: 38px;
    }
}

.hero__wherebased {
    font-size: 16px;
    line-height: 30px;
}

.dark .hero__introduction {
   color: white;  /* Fallback: assume this color ON TOP of image */
   background-image: url(../../img/granit.jpg);
   background-size: 100%;
   background-position: center;
   background-clip: text;
   -webkit-background-clip: text;
   -webkit-text-fill-color: transparent;
}

.arrow-active {
    position: absolute;
    bottom: 100px;
    width: 70px;
    height: 70px;
    left: calc(50% - 35px);
    transform: rotate(45deg);
    animation: blinking-arrow 2s infinite;
}

.dark .arrow-active {
    border: 6px solid #ffffff;
    border-left: transparent;
    border-top: transparent;
}

@keyframes blinking-arrow {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}