.services {
    
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    padding-top: 40px; /*padding here is required to save space for svg image */

    @media (min-width: @tablet) {
      min-height: 100vh;  
    }
    

    &::before {
        position: absolute;
        content: "";
        width: 150px;
        height: 150px;
        top: -114px;
        left: calc(50% - 75px);
        background-image: url("../../img/svg/computer-laptop-icon.svg");
        background-repeat: no-repeat;
        background-position: center center;

        @media (min-width: @tablet) {
            top: 0;
          }
        
          @media (min-width: @desktop) {
            top: 130px;
          }
    }

    &::after {
        position: absolute;
        content: "";
        width: 120px;
        height: 120px;
        bottom: -145px;
        left: calc(50% - 60px);
        background-image: url("../../img/svg/wallet-icon.svg");
        background-repeat: no-repeat;
        background-position: center center;

        @media (min-width: @tablet) {
            bottom: 25px;
          }

        @media (min-width: @desktop) {
            bottom: 130px;
          }
    }
}

.services-description {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-left: 15px;
    padding-right: 15px;
}

.services-description p {
    font-size: 20px;
    text-align: center;

    @media (min-width: @tablet) {
        font-size: 24px; 
        text-align: left; 
      }
}

.arrowdownfooter {
    display: none; 

    @media (min-width: @desktop) {
       bottom: 30px;  
       display: block;
      }
    
}