.header {
    display: flex;
    position: relative;
    padding-top: 15px;
    font-family: "Rubik", "Arial", sans-serif;

    @media (min-width: @tablet) {
    flex-direction: row;
    align-items: start;
    justify-content: space-between;
    font-weight: 700;
    padding-top: 32px;
    }
}

.name {
    width: 50%; 
    text-align: center;
    margin-bottom: 20px;

    @media (min-width: @tablet) {
        text-align: start;
        width: 20%;
        margin-bottom: 0;
    }
}

.name a {
    font-size: 12px;

    @media (min-width: @tablet) {
        font-size: 14px;
    }
}

.page-nav {
    display: flex;
    align-items: start;
    
    @media (max-width: @mobile-only) {
    width: 45%; 
    justify-content: space-around;
    }

    
}

.burger-button {
    display: block;
    background-color: transparent;
    width: 20px;
    height: 20px;
    border: none;
    background-image: url("../../img/svg/burger-icon.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100%;

    &:hover {
        background-image: url("../../img/svg/burger-white.svg");
    }

    @media (min-width: @tablet) {
        display: none;
    }
}




.menu { 
    position: absolute; 
    display: flex;
    flex-direction: column;
    align-items: center;
    top: 0;
    width: 100%;
    left: 0;
    z-index: 2;
    

    @media (min-width: @tablet) {
        position: static;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: start;
    }
}

.hidden {
    display: none;
    @media (min-width: @tablet) {
        display: flex;
    }
    
}

.menu li {
    width: 100%;
    background-color: #d6d6d6;
    text-align: center;
    padding: 13px;
    border-bottom: 2px solid #5b5b5b;

    @media (min-width: @tablet) {
        border-bottom: none;
        background-color: transparent;
        margin-left: 75px;
        margin-bottom: 20px;
        padding: 0;
    }
}

.menu a {
    width: 100%;
    white-space: nowrap;
    color: #111111;
    text-align: center;

    @media (min-width: @tablet) {
        color: #d6d6d6;
        padding: 5px;
    }
}

.page-nav a:hover, 
.page-nav a:focus,
.name a:hover,
.name a:focus {
    color: #ffffff;
    border-bottom: 2px solid #ffffff;
}

.page-nav a:active,
.name a:active {
    opacity:0.5;
}

.themebutton {
    display: block;
    background-color: transparent;
    width: 20px;
    height: 20px;
    border: none;
   
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 100%;
    cursor: pointer;
    position: relative;

    &:active {
        opacity: 0.5;
    }
}

.popup {
    display: none;
}

.popup-light {
    display: none;
}

.close-menu {
        position: absolute; 
        top: 10px;
        right: 10px;
        width: 20px;
        height: 20px;
        border: none;
        background-image: url("../../img/svg/close-line-icon.svg");
        background-size: 100%;
        background-repeat: no-repeat;
        background-position: center;
        background-color: transparent;


    @media (min-width: @tablet) {
        display: none;
    }
}



